import { GuaranteeMethodAdjustmentComponent } from "./guarantee-method-adjustment/guarantee-method-adjustment.component";
import { Routes, RouterModule } from "@angular/router";
import { ModuleWithProviders } from "@angular/core";

export const routes: Routes = [
    {
        path: '',
        children: [
            {path: 'guarantee-method-adjustment',component: GuaranteeMethodAdjustmentComponent},
        ]
    }

]

export const GuaranteeMethodRouting: ModuleWithProviders = RouterModule.forChild(routes);