import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { GuaranteeMethodAdjustmentBean } from "./bean/guarantee-method-adjustment.bean";

@Component({
    selector: 'guaranteeMethodAdjustment',
    templateUrl: './guarantee-method-adjustment.component.html',
    styleUrls: ['./guarantee-method-adjustment.component.scss'],
    providers: [Commfunc,ConfirmationService]
})
export class GuaranteeMethodAdjustmentComponent implements OnInit {
    bean:GuaranteeMethodAdjustmentBean = new GuaranteeMethodAdjustmentBean();
    msgs: Message[];
    ngOnInit() {
        this.toQH(1);
    }

    //用与切换
    elementsModiftDisplay:boolean = false;//账号信息窗口打开
    guaranteeDisplay:boolean = false;//协储员信息窗口打开
    toQH(i){
        if(i==1){
            document.getElementById("elementsModiftDisplay").className ='titBgColor active';
            document.getElementById("guaranteeDisplay").className = 'titBgColor';
            this.elementsModiftDisplay = true;
            this.guaranteeDisplay = false;
        }
        if(i==2){
            document.getElementById("elementsModiftDisplay").className = 'titBgColor';
            document.getElementById("guaranteeDisplay").className = 'titBgColor active';
            this.guaranteeDisplay = true;
            this.elementsModiftDisplay = false;
        }
    }
}