import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { GuaranteeMethodAdjustmentInfoBean } from "./bean/guarantee-method-adjustment-info.bean";
import { CALENDAR_CN } from "../../../../../../@uisftech/common/constant/config";

@Component({
    selector: 'guaranteeMethodAdjustmentInfo',
    templateUrl: './guarantee-method-adjustment-info.component.html',
    styleUrls: ['./guarantee-method-adjustment-info.component.scss'],
    providers: [Commfunc, ConfirmationService]
})
export class GuaranteeMethodAdjustmentInfoComponent implements OnInit {
    bean:GuaranteeMethodAdjustmentInfoBean = new GuaranteeMethodAdjustmentInfoBean();
    msgs: Message[];
    ch: any = CALENDAR_CN;
    guaranteeTotal: string;//总页数
    guaranteeFirst: number = 0;
    guaranteePageNum: number;//当前页数
    guaranteePageSize: number;//每页条数
    guaranteeList: any = [{//担保人信息内容
        "x1": "1",
        "x2": "1",
        "x3": "1",
        "x4": "1",
        "x5": "1",
        "x6": "1",
        "x7": "1"
    }];

    mortgagedTotal: string;//总页数
    mortgagedFirst: number = 0;
    mortgagedPageNum: number;//当前页数
    mortgagedPageSize: number;//每页条数
    mortgagedList: any = [{//抵押物信息内容
        "x1": "1",
        "x2": "1",
        "x3": "1",
        "x4": "1",
        "x5": "1",
        "x6": "1",
        "x7": "1"
    }];

    pledgeTotal: string;//总页数
    pledgeFirst: number = 0;
    pledgePageNum: number;//当前页数
    pledgePageSize: number;//每页条数
    pledgeList: any = [{//质押物信息内容
        "x1": "1",
        "x2": "1",
        "x3": "1",
        "x4": "1",
        "x5": "1",
        "x6": "1",
        "x7": "1"
    }];
    ngOnInit() {
        
    }

    //担保人查询
    guaranteeQuery(){
        this.guaranteePageNum = 1;
        this.guaranteePageSize = 10;
    }

    //担保人翻页
    guaranteePaginate(event){
        //每页显示的条数
        this.guaranteePageSize = event.rows * 1;
        //当前页
        this.guaranteePageNum = event.page + 1;
        this.guaranteeFirst = (this.guaranteePageNum - 1) * this.guaranteePageSize;
        this.guaranteeQuery();
    }

    //抵押物查询
    mortgagedQuery(){
        this.mortgagedPageNum = 1;
        this.mortgagedPageSize = 10;
    }

    //抵押物翻页
    mortgagedPaginate(event){
        //每页显示的条数
        this.mortgagedPageSize = event.rows * 1;
        //当前页
        this.mortgagedPageNum = event.page + 1;
        this.mortgagedFirst = (this.mortgagedPageNum - 1) * this.mortgagedPageSize;
        this.mortgagedQuery();
    }

    //质押物查询
    pledgeQuery(){
        this.pledgePageNum = 1;
        this.pledgePageSize = 10;
    }

    //质押物翻页
    pledgePaginate(event){
        //每页显示的条数
        this.pledgePageSize = event.rows * 1;
        //当前页
        this.pledgePageNum = event.page + 1;
        this.pledgeFirst = (this.pledgePageNum - 1) * this.pledgePageSize;
        this.pledgeQuery();
    }
}