import { GuaranteeMethodAdjustmentCardBean } from "./bean/guarantee-method-adjustment-card.bean";
import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { CALENDAR_CN } from "../../../../../../@uisftech/common/constant/config";

@Component({
    selector: 'guaranteeMethodAdjustmentCard',
    templateUrl: './guarantee-method-adjustment-card.component.html',
    styleUrls: ['./guarantee-method-adjustment-card.component.scss'],
    providers: [Commfunc, ConfirmationService]
})
export class GuaranteeMethodAdjustmentCardComponent implements OnInit {
    bean:GuaranteeMethodAdjustmentCardBean = new GuaranteeMethodAdjustmentCardBean();
    msgs: Message[];
    ch: any = CALENDAR_CN;
    nameList:any = [];
    noPromptDispaly:boolean =false;
    elementList:any = [//修改方式列表 单一
        {label: "担保方式调整", value: "1"}, 
        {label: "担保人调整", value: "2"}
    ];
    singleDisplay:boolean = true;//单一模式
    batchDisplay:boolean = false;//批量模式

    ngOnInit() {
        this.bean.elementNo = "1";
    }

    //单户 和 批量切换
    targetChange(){
        if(this.bean.elementNo=="1"){
            this.singleDisplay= true ;
            this.batchDisplay= false ;
        }
        if(this.bean.elementNo=="2"){
            this.singleDisplay= false ;
            this.batchDisplay= true ;
        }
    }
}